import React from 'react';

export const IVLabel = ({ title, subtitle, record, continuous }) => {
    return <div className="label"><table border="0"><tbody>
        <tr className='heading title'><td colSpan="2">{title}</td></tr>
        <tr className='heading subtitle'><td colSpan="2">{subtitle}</td></tr>
        <tr>
            <td rowSpan="2">
                <IVField {...record.fullName} hideValue/>
                <br/>
                <IVField {...record.fullName} hideLabel/>
            </td>
            <td><IVField {...record.unitRoom} /></td>
        </tr>
        <tr>
            <td><IVField {...record.mrn} /></td>
        </tr>
        <tr>
            <td><IVField {...record.weight}/></td>
            <td><IVField {...record.fin}/></td>
        </tr>
        <tr>
            <td colSpan="2"><IVField {...record.dueDateTime}/></td>
        </tr>
        <tr className='section-start'>
            <td><IVField {...record.drug}/></td>
            <td><IVField {...record.concentration}/></td>
        </tr>
        <tr className='separated'>
            <td colSpan="2"><IVField {...record.baseSolution}/></td>
        </tr>
        <tr className='section-start'>
            <td><IVField {...record.route}/></td>
            <td><IVField {...record.dose}/></td>
        </tr>
        <tr>
            <td>{continuous ? null : <IVField {...record.frequency}/>}</td>
            <td><IVField {...record.totalVolume}/></td>
        </tr>
        <tr>
            <td>{continuous ? null : <IVField {...record.infuseOver}/>}</td>
            <td><IVField {...record.rate}/></td>
        </tr>
        <tr className='separated'>
            <td><IVField {...record.compoundDateTime} hideValue /></td>
            <td><IVField {...record.compoundDateTime} hideLabel /></td>
        </tr>
        <tr className='separated'>
            <td><IVField {...record.expirationDateTime} hideValue /></td>
            <td><IVField {...record.expirationDateTime} hideLabel /></td>
        </tr>
        <tr className="separated">
            <td colSpan="2"><IVField {...record.notes}/><br/> <br/><br/></td>
        </tr>
        <tr>
            <td><IVField {...record.preparedBy}/></td>
            <td><IVField {...record.checkedBy}/></td>
        </tr>
        <tr className='heading'>
            <td colSpan="2">** Return to Phramacy after 24 Hours **</td>
        </tr>
    </tbody></table></div>;
};

const IVField = ({ hideLabel, hideValue, ...props}) => {
    const label = props.printLabel || props.editLabel;

    return <>
        { hideLabel ? null : <strong>{label}:</strong>}
        { hideLabel || hideValue ? null : '  '}
        { hideValue ? null : props.value}
    </>;
};