import React, { useRef, useState } from 'react';
import { ContentIFrame } from './ContentIFrame';
import { IVLabel } from './IVLabel';

export const LabelEditor = () => {
    const iframeRef = useRef();
    const [ state, updateState ] = useState({record: Object.defineProperties({
        firstName: { editLabel: 'First Name', value: 'Nicholas' },
        lastName: { editLabel: 'Last Name', value: 'Goodman' },
        weight: { editLabel: 'Weight (kg)', value: '78' },
        dueDateTime: { editLabel: 'Due Date / Time', printLabel: 'Due D/T', value: '01/16/2021 17:10'},
        unitRoom: { editLabel: 'Unit / Room', printLabel: 'Unit-Rm', value: '40 Bed B'},
        mrn: { editLabel: 'MRN', value: '421759871291-9443'},
        fin: { editLabel: 'FIN' },
        drug: { editLabel: 'Drug' },
        concentration: { editLabel: 'Concentration', printLabel: 'Conc' },
        baseSolution: { editLabel: 'Base Solution', value: 'Sodium Chloride 0.9% 100 ML'},
        route: { editLabel: 'Route', value: 'IVPB' },
        frequency: { editLabel: 'Frequency', printLabel: 'Freq' },
        infuseOver: { editLabel: 'Infuse Over', value: '60 mins' },
        dose: { editLabel: 'Dose' },
        totalVolume: { editlabel: 'Total Volume', printLabel: 'Tot Vol' },        
        rate: { editLabel: 'Rate', value: 'UD mL/hr' },
        compoundDateTime: { editLabel: 'Compound Date / Time', printLabel: 'Compound\nDate/Time', value: '01/16/2021 20:10' },
        expirationDateTime: { editLabel: 'Expiration Date / Time', printLabel: 'Expiration\nDate/Time', value: '01/16/2021 20:10' },
        notes: { editLabel: 'Notes' },
        preparedBy: { editLabel: 'Prepared By', printLabel: 'Prep' },
        checkedBy: { editLabel: 'Checked By', printLabel: 'Check' }

    },{
        fullName: {
            get() {
                return {
                    printLabel: 'Name',
                    value: `${this.lastName.value}, ${this.firstName.value}`
                }
            }
        }
    })});

    const { record } = state;

    const print = () => {
        iframeRef.current.contentWindow.print();
    }

    const onRecordChange = ({ fieldName, value }) => {
        console.log('onRecordChange');
        updateState(current => {
            current.record[fieldName].value = value;
            return { ...current };
        });
    };

    return <div>
        <h1>Label Editor</h1>
        <div style={{ float: 'left', margin: 12}}>
            <EditForm record={record} onChange={onRecordChange} />
            <button onClick={print}>Save and Print</button>
        </div>
        <div style={{ float: 'left'}}>
        <ContentIFrame title="labelContent" ref={iframeRef}>
            <IVLabel 
                title="Children's National Hospital"
                subtitle="** Downtime Label: Intermittent IV **"
                record = {record} 
            />
        </ContentIFrame>
        </div>
    </div>;
}

const EditForm = (props) => (
    <>
        <EditableField {...props} fieldName="firstName" />
        <EditableField {...props} fieldName="lastName" />
        <EditableField {...props} fieldName="drug" />
    </>
);

const EditableField = ({ record, fieldName, onChange }) => {
    const { editLabel, value } = record[fieldName];
    
    const onInputChange = (evt) => {
        onChange({
            fieldName,
            value: evt.target.value
        });
    }

    return <div>
        <div>{editLabel}</div>
        <input value={value} onChange={onInputChange}></input>
    </div>
}

const PrintForm = ({ record }) => {
    const {
        firstName,
        lastName,
        rxDesc
    } = record;

    const fullNameValue = `${lastName.value}, ${firstName.value}`

    return <>
        <PrintableField value={fullNameValue}/>
        <PrintableField value={rxDesc.value}/>
    </>
};

const PrintableField = ({ value }) => (
    <div>{value}</div>
);