import React, { forwardRef, useEffect, useRef, useState } from 'react';
import { render } from 'react-dom';

export const ContentIFrame = forwardRef((props, ref) => {
    const iframeRef = ref || useRef();
    const [ iframe, setIframe ] = useState();

    const frameLoaded = () => {
        setIframe(iframeRef.current);
    };

    useEffect(() => {
        if(!iframe) return;
        render(props.children, iframe.contentDocument.getElementById('root'));
    }, [ iframe, props.children ]);

    return <iframe ref={iframeRef} title={props.title} src="./#/FrameContent" onLoad={frameLoaded} style={{
        width: '3.5in',
        height: '7in'
    }}></iframe>;
});